import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import {
  AppBar,
  Button,
  Container,
  Dialog,
  Grid,
  IconButton,
  Paper,
  Toolbar,
  Typography,
  makeStyles,
} from '@material-ui/core'
import { Alert, AlertTitle } from '@material-ui/lab'
import {
  AccountBalance,
  Assignment,
  Close,
  Group,
  Money,
  MusicNote,
  Person,
  ShoppingCart,
} from '@material-ui/icons'

import EconomySummaryTable from './EconomySummaryTable'
import ListErrors from '../ListErrors'
import PageHeader from '../PageHeader'
import PageWrapper from '../PageWrapper'
import Spinner from '../Spinner'
import StatCard from '../StatCard'
import { SlideUpTransition } from '../../transitions'

import agent from '../../agent'

import {
  ECONOMY_DIALOG_LOADED,
  ECONOMY_DIALOG_UNLOADED,
} from '../../constants/actionTypes'

const useStyles = makeStyles((theme) => ({
  title: {
    color: theme.palette.common.white,
    margin: '0 auto',
  },
}))

const mapStateToProps = (state) => ({
  ...state.economy,
})

const mapDispatchToProps = (dispatch) => ({
  onLoad: () =>
    dispatch({
      type: ECONOMY_DIALOG_LOADED,
      payload: agent.Scores.getAll(),
    }),
  onUnload: () =>
    dispatch({
      type: ECONOMY_DIALOG_UNLOADED,
    }),
})

const Dashboard = ({ data }) => (
  <Grid container spacing={3}>
    <StatCard
      icon={<AccountBalance fontSize='large' />}
      title='Total Points'
      value={data.reduce((prev, cur) => prev + (cur.totals.account || 0), 0)}
    />
    <StatCard icon={<Person fontSize='large' />} title='Top Student'>
      {data[0]?.firstName} {data[0]?.lastName}
    </StatCard>
    <StatCard
      icon={<Assignment fontSize='large' />}
      title='Total Assessment Points'
      value={data.reduce(
        (prev, cur) => prev + (cur.totals.assessments || 0),
        0
      )}
    />
    <StatCard
      icon={<Money fontSize='large' />}
      title='Total Bonus Points'
      value={data.reduce((prev, cur) => prev + (cur.totals.bonuses || 0), 0)}
    />
    <StatCard
      icon={<ShoppingCart fontSize='large' />}
      title='Total Purchase Points'
      value={data.reduce((prev, cur) => prev + (cur.totals.purchases || 0), 0)}
    />
    <StatCard
      icon={<MusicNote fontSize='large' />}
      title='Total Song Requests'
      value={data.reduce((prev, cur) => prev + cur.totals.songs, 0)}
    />
    <StatCard
      icon={<Group fontSize='large' />}
      title='Total Students'
      value={data.length}
    />
    <Grid item xs={12}>
      <Paper elevation={3} style={{ padding: '16px', overflow: 'scroll' }}>
        <Typography gutterBottom variant='h6'>
          Account Summary
        </Typography>
        <EconomySummaryTable accounts={data} />
      </Paper>
    </Grid>
  </Grid>
)

const EconomyDialog = ({ errors, inProgress, onLoad, onUnload, students }) => {
  const [data, setData] = useState([])
  const [open, setOpen] = useState(false)

  const classes = useStyles()

  const handleClickOpen = () => {
    setOpen(true)
    onLoad()
  }

  const handleClose = () => {
    setOpen(false)
    onUnload()
  }

  useEffect(() => {
    setData(students.sort((a, b) => b.totals.account - a.totals.account))
  }, [students])

  return (
    <>
      <Button onClick={handleClickOpen} size='small'>
        View Economy
      </Button>
      <Dialog
        fullScreen
        open={open}
        onClick={(ev) => ev.stopPropagation()}
        onClose={handleClose}
        onFocus={(ev) => ev.stopPropagation()}
        TransitionComponent={SlideUpTransition}
      >
        <AppBar position='relative'>
          <Toolbar>
            <IconButton edge='start' onClick={handleClose}>
              <Close />
            </IconButton>
            <Typography variant='h5' className={classes.title}>
              Accounts
            </Typography>
          </Toolbar>
        </AppBar>
        <PageWrapper>
          <Container>
            <PageHeader title='Economy View' />
            <ListErrors errors={errors} />
            {inProgress ? (
              <Spinner message='Computing...' />
            ) : data.length > 0 ? (
              <Dashboard data={data} />
            ) : (
              <Alert severity='info'>
                <AlertTitle>No data available</AlertTitle>
                You must create at least one course, add students to that
                course, and add bonuses, purchases, or scores before any data
                will be visible here
              </Alert>
            )}
          </Container>
        </PageWrapper>
      </Dialog>
    </>
  )
}

export default connect(mapStateToProps, mapDispatchToProps)(EconomyDialog)
